<template>
  <div>
    <p></p>
    <el-button type="primary" icon="el-icon-edit" @click="openMsg"></el-button>
    <el-col :span="24">
      <el-table :data="infoData" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="info-table-expand">
              <el-form-item label="">
                <span>{{props.row.id}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{props.row.name}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{props.row.content}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{props.row.startTime}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{props.row.finishTime}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{props.row.remark}}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="id"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="content"></el-table-column>
      </el-table>
    </el-col>
  </div>
</template>
<style>
  .info-table-expand {
    font-size: 0;
  }
  .info-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .info-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>
<script>
  export default {
    name: 'plan',
    data (){
      return {
        infoData: [{
          id: 'P0001',
          name: 'A',
          content: '',
          startTime: '2018-04-20 08:00:00',
          finishTime: '2018-04-20 09:00:00',
          remark: ''
        },
          {
            id: 'P0002',
            name: 'B',
            content: '',
            startTime: '2018-04-19 08:00:00',
            finishTime: '2018-04-19 12:00:00',
            remark: ''
          },
          {
            id: 'P0003',
            name: 'C',
            content: '',
            startTime: '2018-04-18 08:00:00',
            finishTime: '2018-04-18 09:00:00',
            remark: ''
          },
          {
            id: 'P0004',
            name: 'D',
            content: '',
            startTime: '2018-04-17 08:00:00',
            finishTime: '2018-04-17 11:00:00',
            remark: ''
          },
          {
            id: 'P0005',
            name: 'E',
            content: '',
            startTime: '2018-04-16 08:00:00',
            finishTime: '2018-04-16 23:00:00',
            remark: ''
          }]
      }
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
      }
    }
  }
</script>



