import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

/**/
const date = 'Mon Mar 24 2018 00:00:00 GMT+0800 ()'
const data = [
  {
    id: '1111',
    name: 'Allen',
    type: '',
    status: ''
  },{
    id: '2222',
    name: 'Thomas',
    type: '',
    status: ''
  }
]

const state = {
  topNavState: 'home',
  leftNavState: 'home'
}

/**/
for(var item in state) {
  localStorage.getItem(item)? state[item] = JSON.parse(localStorage.getItem(item)): false;
}

export default new Vuex.Store({
  state
})



