<template>
  <div>
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <el-col :span="24" offset="10"><h1 style="padding-left: 32px;padding-bottom: 20px;font-size: 30px" ></h1></el-col>
      <el-col :span="24" offset="6" class="toolbar" style="padding-bottom: 0;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.number" placeholder="()" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input  placeholder="" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table :data="tableData" :row-class-name="tableRowClassName" border style="width: 100%">
        <el-table-column prop="mId" v-if="idShow" label="ID"></el-table-column>
        <el-table-column prop="mNumber" label="" width="150" sortable align="center"></el-table-column>
        <el-table-column prop="name" label="" width="200" align="center"></el-table-column>
        <el-table-column prop="mType" label="" width="120" align="center"></el-table-column>
        <el-table-column prop="mContent" label="" show-overflow-tooltip width="100" align="center"></el-table-column>
        <el-table-column prop="createTime" label="" width="150" align="center"></el-table-column>
        <el-table-column prop="depart" label="" width="100" align="center"></el-table-column>
        <el-table-column prop="factory" label="" width="150" align="center"></el-table-column>
        <el-table-column prop="eRemark" label="" width="180" align="center"></el-table-column>
        <el-table-column label="" align="center" width="180">
          <template slot-scope="scope">
            <el-button
              size="mini" @click="handleDetail(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </el-col>

    <el-dialog title="" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" placeholder="" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.depart" placeholder="">
            <el-option label="1" value="receive"></el-option>
            <el-option label="2" value="forward"></el-option>
            <el-option label="3" value="receive"></el-option>
            <el-option label="4" value="forward"></el-option>
            <el-option label="5" value="receive"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="">
            <el-option label="PLC" value="receive"></el-option>
            <el-option label="IPC" value="forward"></el-option>
            <el-option label="RTU" value="receive"></el-option>
            <el-option label="DSC" value="forward"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="IP" :label-width="formLabelWidth">
          <el-input v-model="form.ip" placeholder="IP" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="MAC" :label-width="formLabelWidth">
          <el-input v-model="form.mac" placeholder="MAC" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.remark" type="textarea" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-date-picker v-model="form.createTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="dialogAddVisible = false"></el-button>
        <el-button type="primary" @click="openMsg"></el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import Header from "./header";
  export default {
    name: 'deviceopration',
    components: {Header},
    data: function(){
      return {
        loading: false,
        radio: '1',
        idShow: false,
        keyword: "",
        total:6,
        currentPage: 1,
        pageSize: 10,
        tableData: [{
          mId: 1,
          mNumber: 'PLC_001_001',
          name: 's7_200 PLC OCSBV001',
          mType: 'PLC',
          mContent: '',
          eRemark: '',
          createTime: '2016-11-17',
          depart: '001',
          factory: ''
        },
          {
            mId: 2,
            mNumber: 'PLC_001_002',
            name: 's7_200 PLC OCSBV002',
            mType: 'PLC',
            mContent: '',
            eRemark: '',
            createTime: '2016-11-17',
            depart: '001',
            factory: ''
          },{
            mId: 3,
            mNumber: 'PLC_001_003',
            name: 's7_400 PLC MMI002',
            mType: 'PLC',
            mContent: '',
            eRemark: '',
            createTime: '2016-11-17',
            depart: '001',
            factory: ''
          },{
            mId: 4,
            mNumber: 'PLC_001_004',
            name: 'Modbus Alex002',
            mType: 'PLC',
            mContent: '',
            eRemark: '',
            createTime: '2016-11-17',
            depart: '001',
            factory: ''
          },{
            mId: 5,
            mNumber: 'PLC_001_005',
            name: 'Modbus Alex003',
            mType: 'PLC',
            mContent: '',
            eRemark: '',
            createTime: '2016-11-19',
            depart: '001',
            factory: ''
          },{
            mId: 6,
            mNumber: 'PLC_001_006',
            name: 'Bacnet Css581_01',
            mType: 'PLC',
            mContent: '',
            eRemark: '',
            createTime: '2016-11-18',
            depart: '001',
            factory: ''
          }],
        form: {
          name: '',
          type: '',
          depart: '',
          remark: '',
          createTime: '',
          ip: '',
          mac: '',
        },
        formLabelWidth: '120px',
        dialogAddVisible: false,
        filters: {
          number: ''
        }
      };
    },
    methods: {
      handleSearch(){
        console.info(this.filters.number);
      },
      handleDetail(index, row) {
        console.log(index, row);
        console.log(row.mId);
        this.$router.push('/devicesinfo'); //go
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1 || rowIndex === 2 || rowIndex === 3 || rowIndex === 4) {
          return 'medium-row';
        }
        return '';
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val){
//        this.pageSize = val;
//        this.currentPage = 1;
      },
      handleCurrentChange(val){
//        this.currentPage = val;
      },
      showAddDialog(){
        this.dialogAddVisible = true;
      },
      handleClose(done){  //
        done();
      },
      openMsg() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        })
        this.dialogAddVisible = false;

      }
    }
  }
</script>
<style>
  .el-table .medium-row {
    background: #fff6f6;
  }
  .el-table .low-row {
    background: #fffdf4;
  }
  .el-table .high-row {
    background: #ffdad8;
  }
</style>



