<template>
  <div>
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.number" placeholder="" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="mId" v-if="idShow" label="ID"></el-table-column>
        <el-table-column prop="mNumber" label="" width="180" sortable></el-table-column>
        <el-table-column prop="mType" label="" width="180"></el-table-column>
        <el-table-column prop="mContent" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="eRemark" label="" width="180"></el-table-column>
        <el-table-column prop="createTime" label="" width="180"></el-table-column>
        <el-table-column prop="updateTime" label="" width="180"></el-table-column>
        <el-table-column prop="recordStatus" label="" width="180"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleDetail(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </el-col>

    <el-dialog title="" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.number" placeholder="" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="">
            <el-option label="" value="receive"></el-option>
            <el-option label="" value="forward"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio-group v-model="form.content">
            <el-radio label="1"></el-radio>
            <el-radio label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.remark" type="textarea" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-date-picker v-model="form.createTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="dialogAddVisible = false"></el-button>
        <el-button type="primary" @click.native="editFormSubmit"></el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  export default {
    name: 'mission',
    data: function(){
      return {
        loading: false,
        radio: '1',
        idShow: false,
        keyword: "",
        total: 2,
        currentPage: 1,
        pageSize: 10,
        tableData: [{
          mId: 1,
          mNumber: '10001',
          mType: '',
          mContent: '',
          eRemark: '',
          createTime: '2017-03-27',
          updateTime: '2016-03-27',
          recordStatus: '1'
        },
          {
            mId: 2,
            mNumber: '10002',
            mType: '',
            mContent: '',
            eRemark: '',
            createTime: '2017-03-27',
            updateTime: '2016-03-27',
            recordStatus: '1'
          }],
        form: {
          number: '',
          type: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        formLabelWidth: '120px',
        dialogAddVisible: false,
        filters: {
          number: ''
        }
      };
    },
    methods: {
      handleSearch(){
        console.info(this.filters.number);
      },
      handleDetail(index, row) {
        console.log(index, row);
        console.log(row.mId);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val){
//        this.pageSize = val;
//        this.currentPage = 1;
      },
      handleCurrentChange(val){
//        this.currentPage = val;
      },
      showAddDialog(){
        this.dialogAddVisible = true;
      },
      handleClose(done){  //
        done();
      }
    }
  }
</script>



