<template>
  <div style="margin-top: 20px;margin-left: 2%;width: 95%" align="center" >
    <strong style="font-family: ;font-size: x-large;text-align: center"></strong>
    <div class="select">
      <el-form inline="true" size="mini" model="selectItem">
        <el-form-item label="">
          <el-input v-model="selectItem.user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="selectItem.factory">
            <el-option v-for="item in factory_list" :value="item" :key="item" :label="item.label">{{item}}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="selectItem.position" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="selectItem.depart" placeholder="">
            <el-option v-for="item in depart_list" :value="item" :key="item">{{item}}</el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <el-form label-position="center">
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
        <el-form-item>
          <h3>{{select_callback_info}}</h3>
        </el-form-item>
      </el-form>
      <div class="btndiv">
        <el-dialog title="" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.nickname" value="45765trfd"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.realname" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-radio-group v-model="form.content">
                <el-radio label="1"></el-radio>
                <el-radio label="2"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-date-picker v-model="form.createTime" type="date" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="form.factory">
                <el-option label="" value="liaoning"></el-option>
                <el-option label="" value="guangdong"></el-option>
                <el-option label="" value="beijing"></el-option>
                <el-option label="" value="haerbing"></el-option>
                <el-option label="" value="jilin"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="form.department" placeholder="">
                <el-option label="" value="zonghe"></el-option>
                <el-option label="" value="caiwu"></el-option>
                <el-option label="" value="anquan"></el-option>
                <el-option label="" value="ergong"></el-option>
                <el-option label="" value="gongcheng"></el-option>
                <el-option label="" value="shengchan"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.text" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.remark" type="textarea" placeholder="300"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="dialogAddVisible = false"></el-button>
            <el-button type="primary" @click="openMsg"></el-button>
          </div>
        </el-dialog>
        <el-button class="mybutton"  size="large" @click="showAddDialog" icon="el-icon-third-tianjiayonghu"> </el-button>
        <el-button class="mybutton" @click="deleteUser" size="large" icon="el-icon-third-delete_member"> </el-button>
        <el-button class="mybutton" @click="show3 = !show3" size="large" icon="el-icon-third-role"> </el-button>

        <el-collapse-transition  name="slide-fade">
          <div v-show="show3">
            <el-radio v-model="role" label="1"></el-radio>
            <el-radio v-model="role" label="2"></el-radio>
            <el-radio v-model="role" label="3"></el-radio>
            <el-radio v-model="role" label="4"></el-radio>
            <el-button type="primary" @click="dispatch" style="margin-left: 20px;height: 30px;margin-top: 5px;text-align: center"></el-button>
          </div>
        </el-collapse-transition>
        <el-button class="mybutton" @click="refresh_table" icon="el-icon-third-refresh-1-copy"></el-button>
      </div>

      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" border="true" height="250">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" prop="user" ></el-table-column>
        <el-table-column prop="name" label="" ></el-table-column>
        <el-table-column prop="sex" label="" ></el-table-column>
        <el-table-column prop="birthday" label="" p></el-table-column>
        <el-table-column prop="factory" label="" ></el-table-column>
        <el-table-column prop="depart" label="" ></el-table-column>
        <el-table-column prop="job" label="" ></el-table-column>
        <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
        <el-table-column fixed="right" label="" width="120">
          <template slot-scope="scope">
            <el-button @click="showAddDialog" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>

<script>
  const cityOptions = ['', '', '', ''];
  export default {
    data() {
      return {
        role:'',
        total:2,
        formLabelWidth: '120px',
        dialogAddVisible:false,
        isIndeterminate: true,
        show3:false,
        selectItem:{
          user:'',
          factory:'',
          position:'',
          depart:''
        },
        form: {
          nickname:'',
          realname:'',
          factory: '',
          text: '',
          department: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        factory_list:['','','','',''],
        depart_list:['','','','','',''],
        tableData: [{
          user: 'dgefd',
          name: '',
          sex: '',
          birthday: '1995-02-23',
          factory: '',
          depart: '',
          job: '',
          address: ''
        }, {
          user: 'xjmie',
          name: '',
          sex: '',
          birthday: '1995-02-23',
          factory: '',
          depart: '',
          job: '',
          address: ''
        }]
      }
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      dispatch() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.show3 = !this.show3;
      },
      onSubmit() {
        console.log('submit!');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      showAddDialog(){
        console.log(this.dialogAddVisible);
        this.dialogAddVisible = true;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleClose(done){  //
        done();
      }
    }
  }
</script>
<style>
  .select{
    position: relative;
    width: 100%;
    margin: 0 auto;
    background:#fff;
    padding: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  .btndiv{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: left;
  }
  .mybutton{
    display: inline-block;
    margin-left:5px;
    margin-right: 5px;
  }
</style>




