<!--  -->
<style lang="stylus" scoped>
  .relation
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    .main
      height calc(100% - 120px)
      width 100%
      transition all 0.5s linear
</style>

<template lang="html">
  <div class="relation">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>-->
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  const USER_NAME = 'elastic'
  const PSW = 'elasticl@ethical.cn'
  const AUTH_TOKEN = "Basic " + btoa(USER_NAME + ":" + PSW)


  export default {
    created() {
      this._getCityData()
    },
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        reData: {},
        name: ''
      }
    },
    methods: {
      _init(options) {
        this.myChart = echarts.init(document.querySelector('.relation .main'))
        this.myChart.setOption(options)
        this.legendArr = options.series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      },
      _getCityData() {
        axios.get('static/data/flar.json').then((res) => {
          this.reData = res.data
          this.$nextTick(() => {
            this._getMyChart()
          })
        })
      },
      _getMyChart() {
        let option = {
          title:{
            text:"SWAT",
            textAlign:'left',
            textStyle: {//
              color: '#000000'//
            }

          },
          tooltip: {    //
            trigger: 'item',    //item'axis''none':
            triggerOn: 'mousemove'    //mousemove|clickmousemoveclicknone
          },
          series: [    //
            {
              type: 'tree',    //

              data: [this.reData],    //flare.json

              top: '1%',       //
              left: '20%',      //
              bottom: '1%',    //
              right: '20%',    //

              symbolSize: 14,   //7

              label: {         //
                normal: {
                  position: 'left',       //
                  verticalAlign: 'middle',//topmiddlebottom
                  align: 'right',         //topcenterbottom
                  fontSize: 14             //
                }
              },

              leaves: {    //
                label: {
                  normal: {
                    position: 'right',
                    verticalAlign: 'middle',
                    align: 'left'
                  }
                }
              },

              expandAndCollapse: true,    //
              animationDuration: 550,     //,1000
              animationDurationUpdate: 750//300
            }
          ]
        };
        this._init(option)


      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    }
  }

</script>



