<template>
  <el-button type="primary" class="button" @click="buttoncli"></el-button>
</template>

<script>
  import screenfull from 'screenfull'
  //	:1.,npm install --save screenfull
  //	         2.
  //	         3.,,screenfull.toggle()
  export default{
    data(){
      return{
        isFullscreen: false
      }
    },
    methods:{
      buttoncli(){
        if (!screenfull.enabled) { // 
          this.$message({
            message: '',
            type: 'warning'
          })
          return false
        }
        screenfull.toggle()
        this.$message({
          message: '',
          type: 'success'
        })
      }
    }
  }
</script>



