<template>
  <div style="margin-top: 20px;margin-left: 2%;width: 95%" align="center" >
    <strong style="font-family: ;font-size: x-large;text-align: center"></strong>
    <div class="select">
      <el-form inline="true" size="mini" model="selectItem">
        <el-form-item label=":">
          <el-input v-model="selectItem.type" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="selectItem.user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model="selectItem.status" placeholder="">
            <el-option label="" value="zonghe"></el-option>
            <el-option label="" value="caiwu"></el-option>
            <el-option label="" value="anquan"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-form label-position="center">
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
        <el-form-item>
          <h3>{{select_callback_info}}</h3>
        </el-form-item>
      </el-form>
      <div class="btndiv">
        <el-dialog title="" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.name" value="45765trfd"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.type" auto-complete="off"></el-input>
            </el-form-item>
            <!--<el-form-item label="" :label-width="formLabelWidth">-->
            <!--<el-radio-group v-model="form.content">-->
            <!--<el-radio label="1"></el-radio>-->
            <!--<el-radio label="2"></el-radio>-->
            <!--</el-radio-group>-->
            <!--</el-form-item>-->
            <el-form-item label="" :label-width="formLabelWidth">
              <el-date-picker v-model="form.createTime" type="date" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.discription" type="textarea" placeholder="300"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.detail" type="textarea" placeholder="300"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.rule" type="textarea" placeholder="300"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="dialogAddVisible = false"></el-button>
            <el-button type="primary" @click="openMsg"></el-button>
          </div>
        </el-dialog>

        <el-dialog title="" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogdescripption" :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">w_001</el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">sat</el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">2018-11-16</el-form-item>
            <el-form-item label="" :label-width="formLabelWidth"></el-form-item>
            <el-form-item label="" :label-width="formLabelWidth"></el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">plc</el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">alert tcp any any -> 192.168.1.0/24 111 (content:"|00 01 86 a5|"; msg: "mountd access";)</el-form-item>
          </el-form>
          <!--<div slot="footer" class="dialog-footer">-->
          <!--<el-button @click.native="dialogdescripption = false"></el-button>-->
          <!--<el-button type="primary" @click="openMsg"></el-button>-->
          <!--</div>-->
        </el-dialog>

        <el-button class="mybutton"  size="large" @click="showAddDialog" icon="el-icon-circle-plus-outline"> </el-button>
        <el-button class="mybutton" @click="deleteUser" size="large" icon="el-icon-delete"> </el-button>
        <el-button class="mybutton" @click="startUse" size="large" icon="el-icon-setting"></el-button>

        <el-collapse-transition  name="slide-fade">
          <div v-show="show3">
            <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" style="float: left;margin-top: 10px" >
              <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
            </el-checkbox-group>
            <el-button type="primary" @click="sumbit" style="margin-left: 20px;height: 30px;margin-top: 5px;text-align: center"></el-button>
          </div>
        </el-collapse-transition>
        <el-button class="mybutton" @click="refresh_table" icon="el-icon-third-refresh-1-copy"></el-button>
      </div>

      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" border="true" height="250">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" prop="id" ></el-table-column>
        <el-table-column prop="name" label="" ></el-table-column>
        <el-table-column prop="type" label="" ></el-table-column>
        <el-table-column prop="people" label="" p></el-table-column>
        <el-table-column prop="dictionary" label="" ></el-table-column>
        <el-table-column prop="status" label="" ></el-table-column>
        <el-table-column prop="date" label="" ></el-table-column>
        <!--<el-table-column prop="job" label="" ></el-table-column>-->
        <!--<el-table-column prop="detail" label="" show-overflow-tooltip></el-table-column>-->
        <el-table-column prop="detail" label="" width="120">
          <template slot-scope="scope">
            <el-button @click="showDetail" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>

<script>
  const cityOptions = ['', '', '', ''];
  export default {
    data() {
      return {
        total:2,
        checkedCities: [],
        formLabelWidth: '120px',
        dialogAddVisible:false,
        dialogdescripption:false,
        cities: cityOptions,
        isIndeterminate: true,
        show3:false,
        selectItem:{
          user:'',
          type:'',
          status:''
        },
        form: {
          name:'',
          type:'',
          detail: '',
          rule: '',
          discription: '',
          createTime: '',
        },
        tableData: [{
          id: 'w_001',
          name: 'sat',
          type:"s",
          people: '',
          dictionary: '',
          date: '2018-11-16',
          detail: ''
        }, {
          id: 'w_002',
          name: 'plc',
          type:"s",
          people: '',
          dictionary: '',
          date: '2018-11-17',
          detail: ''
        }]
      }
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      startUse() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
      },
      onSubmit() {
        console.log('submit!');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      showAddDialog(){
        console.log(this.dialogAddVisible);
        this.dialogAddVisible = true;
      },
      showDetail(){
        console.log(this.dialogdescripption);
        this.dialogdescripption = true;

      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleClose(done){  //
        done();
      }
    }
  }
</script>
<style>
  .select{
    position: relative;
    width: 100%;
    margin: 0 auto;
    background:#fff;
    padding: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  .btndiv{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: left;
  }
  .mybutton{
    display: inline-block;
    margin-left:5px;
    margin-right: 5px;
  }
</style>




