<template>
  <div style="margin-top: 40px;margin-left: 5%">
    <strong style="font-family: ;font-size: xx-large"></strong>
    <hr style="margin-top: 40px" width="96%"/>
    <el-col :span="23" class="warp-main" v-loading="loading" element-loading-text="" style="margin-top: 15px">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <el-upload align="center"
                       class="avatar-uploader"
                       action="https://jsonplaceholder.typicode.com/posts/"
                       :show-file-list="false"
                       :on-success="handleAvatarSuccess"
                       :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
            </el-upload>
            <a href="#" style="display: block;font-family: ;font-size: large;text-align: center;margin-top: 10px"></a>
          </div>
        </el-col>
        <el-col :span="20">
          <el-row  style="margin-left: 10px;">
            <el-col span="24" style="margin-bottom: -10px;">
              <el-form label-position="left" class="info-table-id">
                <el-form-item label="ID">
                  <span>001_001</span>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="3" style="margin-top: -20px">
              <el-form>
                <el-form-item label="">
                  <span></span>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="1" style="margin-top: -20px">
              <el-form>
                <el-form-item label="|">
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="5" style="margin-top: -20px">
              <el-form>
                <el-form-item label="">
                  <span>132</span>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="24">
              <hr style="margin-top: -15px;"/>
            </el-col>
          </el-row>
          <el-row>
            <el-col span="21" style="margin-left: 10px">
              <div class="grid-content">
                <el-form label-position="left" class="info-table-expand">
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span>admin</span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span>1995-10-09</span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                </el-form>
              </div>
            </el-col>
            <el-col span="2">
              <a href="#" @click="showAddDialog" style="float: right;font-family: ;font-size: large"></a>
            </el-col>
          </el-row>
        </el-col>

      </el-row>

    </el-col>

    <el-dialog title="" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.nickname" value="45765trfd"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.realname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio-group v-model="form.content">
            <el-radio label="1"></el-radio>
            <el-radio label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-date-picker v-model="form.createTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.factory">
            <el-option label="" value="liaoning"></el-option>
            <el-option label="" value="guangdong"></el-option>
            <el-option label="" value="beijing"></el-option>
            <el-option label="" value="haerbing"></el-option>
            <el-option label="" value="jilin"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.department" placeholder="">
            <el-option label="" value="zonghe"></el-option>
            <el-option label="" value="caiwu"></el-option>
            <el-option label="" value="anquan"></el-option>
            <el-option label="" value="ergong"></el-option>
            <el-option label="" value="gongcheng"></el-option>
            <el-option label="" value="shengchan"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.text" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.remark" type="textarea" placeholder="300"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="dialogAddVisible = false"></el-button>
        <el-button type="primary" @click="openMsg"></el-button>
      </div>
    </el-dialog>

  </div>
</template>
<style>
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .info-table-expand {
    font-size: 20px;
  }
  .info-table-expand label {
    width: 90px;
  }
  .info-table-id {
    font-size: 20px;
    margin-bottom: 0%;
  }
  .info-table-id label {
    width: 90px;
    color: #99a9bf;
  }
  .info-table-id span {
    width: 90px;
    color: #99a9bf;
  }
</style>
<script>
  export default {
    name: 'mission',
    data: function(){
      return {
        loading: false,
        radio: '1',
        idShow: false,
        keyword: "",
        total: 2,
        currentPage: 1,
        pageSize: 10,
        form: {
          nickname:'admin',
          realname:'',
          factory: '',
          department: '',
          content: '1',
          remark: '',
          text:'',
          createTime: '1995-10-09',
        },
        formLabelWidth: '120px',
        dialogAddVisible: false,
        filters: {
          number: ''
        },
        imageUrl: '../static/img/bg.jpg'
      };
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      handleSearch(){
        console.info(this.filters.number);
      },
      handleDetail(index, row) {
        console.log(index, row);
        console.log(row.mId);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val){
//        this.pageSize = val;
//        this.currentPage = 1;
      },
      handleCurrentChange(val){
//        this.currentPage = val;
      },
      showAddDialog(){
        this.dialogAddVisible = true;
      },
      handleClose(done){  //
        done();
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
  }
</script>



