<!--  -->
<style lang="stylus" scoped>
  .radar
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="radar">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        name: '',






        lineStyle : {
          normal: {
            width: 1,
            opacity: 0.5
          }
        },

        option : {
      //    backgroundColor: '#101736',
          color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0'],
          title: {
            text: '',
            left: 'left',
            top: 20,
            textStyle: {
              color: '#000000'
            }
          },
          legend: {
            show: true,
            // icon: 'circle',//

            bottom:0,
            center: 0,
            itemWidth: 14, // [ default: 25 ]
            itemHeight: 14, // [ default: 14 ]
            itemGap: 21, // [ default: 10 ]
            textStyle: {
              fontSize: 20,
              color: '#ade3ff'
            },
            data: ['1', '2', '3','4','5'],
          },
          radar: [{

            indicator: [
              {
                text: '',
                max: 100
              },
              {
                text: '',
                max: 100
              },
              {
                text: '',
                max: 100
              },
              {
                text: '',
                max: 100
              },
              {
                text: '',
                max: 100
              },
              {
                text: '',
                max: 100
              }
            ],

            textStyle: {
              color: 'red'
            },
            center: ['50%', '50%'],
            radius:  '75%',
            startAngle: 90,
            splitNumber: 3,
            orient: 'horizontal', // ,'horizontal','vertical'.
            // shape: 'circle',
            // backgroundColor: {
            //     image:imgPath[0]
            // },
            name: {
              formatter: '{value}',
              textStyle: {
                fontSize: 20, //
                color: '#150008' //
              }
            },
            splitArea: { //  grid 
              show: false,
              areaStyle: { // 
                color: ['#a9a9a9', '#A9A9A9'], // 
              }
            },
            // axisLabel:{//
            //     show: true
            // },
            axisLine: { //
              lineStyle: {
                color: '#627c94'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#f0fcff', // 
                width: 1, // 
              }
            }
          }, ],
          series: [{
            name: '',
            type: 'radar',
            itemStyle: {
              emphasis: {
                lineStyle: {
                  width: 8
                }
              }
            },
            data: [
              {
                name: '1',
                value: [55, 65, 75, 90, 22, 55],
                areaStyle: {
                  normal: { // 
                    color: {
                      type: 'linear',
                      x: 0, //
                      y: 0, //
                      x2: 1, //
                      y2: 1, //
                      colorStops: [{
                        offset: 0,
                        color: '#325B69'/***********/
                      }, {
                        offset: 0.5,
                        color: '#325B69'
                      }, {
                        offset: 1,
                        color: '#325B69'/*****************/
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6// 
                  }
                },
                symbolSize: 4.5, //  10  [20, 10] 2010
                label: {                    // 
                  normal: {
                    show: false,             // [ default: false ]
                    position: 'top',        // [ default: top ]
                    distance: 2,            //  position  'top''insideRight'[ default: 5 ]
                    color: '#150008',          //  'auto'[ default: "#fff" ]
                    fontSize: 14,           // 
                    formatter:function(params) {
                      return params.value;
                    }
                  }
                },
                itemStyle: {
                  normal: { //
                    borderColor: '#325B69',
                    borderWidth: 4.5
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 
                //     }
                // }
              },
              {
                name: '2',
                value: [50, 20, 45, 30, 75, 32],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#698570',
                    borderWidth: 4.5,
                  }
                },
                areaStyle: {
                  normal: { // 
                    color: {
                      type: 'linear',
                      x: 0, //
                      y: 0, //
                      x2: 1, //
                      y2: 1, //
                      colorStops: [{
                        offset: 0,
                        color: '#698570'
                      }, {
                        offset: 0.5,
                        color: '#698570'
                      }, {
                        offset: 1,
                        color: '#698570'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6 // 
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 
                //     }
                // }
              },
              {
                name: '3',
                value: [37, 80, 12, 50, 25, 43],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#AE5548',
                    borderWidth: 4.5,
                  }
                },
                areaStyle: {
                  normal: { // 
                    color: {
                      type: 'linear',
                      x: 0, //
                      y: 0, //
                      x2: 1, //
                      y2: 1, //
                      colorStops: [{
                        offset: 0,
                        color: '#AE5548'
                      }, {
                        offset: 0.5,
                        color: '#AE5548'
                      }, {
                        offset: 1,
                        color: '#AE5548'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6 // 
                  }
                }
              }
              ,{
                name: '4',
                value: [50, 20, 45, 70, 85, 90],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#6D9EA8',
                    borderWidth: 4.5,
                  }
                },
                areaStyle: {
                  normal: { // 
                    color: {
                      type: 'linear',
                      x: 0, //
                      y: 0, //
                      x2: 1, //
                      y2: 1, //
                      colorStops: [{
                        offset: 0,
                        color: '#6D9EA8'
                      }, {
                        offset: 0.5,
                        color: '#6D9EA8'
                      }, {
                        offset: 1,
                        color: '#6D9EA8'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6 // 
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 
                //     }
                // }
              },
              {
                name: '5',
                value: [50, 20, 45, 30, 66, 51],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#9CC2B0',
                    borderWidth: 2.5,
                  }
                },
                areaStyle: {
                  normal: { // 
                    color: {
                      type: 'linear',
                      x: 0, //
                      y: 0, //
                      x2: 1, //
                      y2: 1, //
                      colorStops: [{
                        offset: 0,
                        color: '#9CC2B0'
                      }, {
                        offset: 0.5,
                        color: '#9CC2B0'
                      }, {
                        offset: 1,
                        color: '#9CC2B0'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.5 // 
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 
                //     }
                // }
              },]
          }, ]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // domecharts
      this.myChart = echarts.init(document.querySelector('.radar .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



